import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

const ScrollToTop = () => {
    const { pathname } = useLocation();

    useEffect(() => {
        const scrollContainer = document.querySelector('.content-container'); // Cun phn t c th
        if (scrollContainer) {
            scrollContainer.scrollTo(0, 0); // Cun v u trong khu vc
        } else {
            window.scrollTo(0, 0); // Cun ton trang nu khng tm thy phn t
        }
    }, [pathname]);

    return null;
};

export default ScrollToTop;
